import axios from 'axios'
export default {
  name: 'projectFrame',
  data () {
    return {
      PR: String,
      head: Array,
      activeNames: ['1']
    }
  },
  methods: {
    getFramework () {
      axios.get('/api/framework.json')
        .then(this.getFrameworkSucc)
    },
    getFrameworkSucc (res) {
      res = res.data[0].frame
      if (res) {
        const data = res
        this.PR = data.PR
        this.head = data.head
        console.log(this.PR)
      }
    },
    handleChange (val) {
      console.log(val)
    }
  },
  mounted () {
    this.getFramework()
  }
}



